import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom"
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import { Empty, Collapse, Select, Row, Col, Divider, DatePicker, Tooltip } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/pettyCashReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  // StatusTag,
  customCollapseStyle,
  // StatusBadge,
  // TransNo 
} from "../../../App/app.style";

import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectPC = "/dashboard/financials/petty-cashes";

class PettyCashReport extends Component {
  state = {
    filter: {
      start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null,
      id_petty_cash_type: null
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getPettyCashReports } = this.props;
    getPettyCashReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.pettyCashReport.error) {
      notification("error", this.props.pettyCashReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.pettyCashReport.success) {
      notification("success", this.props.pettyCashReport.success);
      this.props.clearSuccess();
    }
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    }
    else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    }
    else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_petty_cash_type) {
      string += "id_petty_cash_type=" + filter.id_petty_cash_type;
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location)
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
          "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
          "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getPettyCashReports } = this.props;
    //const { current_page } = this.props.pettyCashReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getPettyCashReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPettyCashReports } = this.props;
    this.setState({
      filter: {
        start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_location: null,
        id_petty_cash_type: null
      }
    });
    getPettyCashReports();
  };

  render() {
    // Data setup
    const { petty_cash_reports, locations, loading, loadingForeign, petty_cash_types, filter, filter_select } =
      this.props.pettyCashReport ? this.props.pettyCashReport : [];
    //console.log(filter);

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        date: true,
        rowKey: "transaction_date",
        width: "12%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "pettyCashes.no" }),
        dataIndex: "petty_cash_no",
        rowKey: "petty_cash_no",
        width: "18%",
        render: (text, row) => {
          if (isNaN(row.transaction_type)) {
            return {
              children: <span><strong>{row.transaction_type}</strong></span>,
              props: {
                colSpan: 5,
              },
            }
          }
          else {
            return (
              <Link to={`${redirectPC}/update/${row.id_petty_cash}`}>
                {text}
              </Link>
            )
          }

        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "18%",
        render: (text, row) => {
          if (isNaN(row.transaction_type)) {
            return {
              props: {
                colSpan: 0,
              },
            }
          }
          else {
            if (text && text.length > 25) {
              return (
                <Tooltip placement="bottom" title={text}>
                  {text.substring(0, 25) + "..."}
                </Tooltip>
              );
            } else {
              return text;
            }
          }
        }
      },
      {
        title: getLanguage({ id: "pettyCashType" }),
        dataIndex: "description",
        rowKey: "description",
        width: "18%",
        render: (text, row) => {
          if (isNaN(row.transaction_type)) {
            return {
              props: {
                colSpan: 0,
              },
            }
          }
          else {
            if (text && text.length > 25) {
              return (
                <Tooltip placement="bottom" title={text}>
                  {text.substring(0, 25) + "..."}
                </Tooltip>
              );
            } else {
              return text;
            }
          }
        }
      },
      {
        title: getLanguage({ id: "deposit" }),
        dataIndex: "deposit",
        rowKey: "deposit",
        width: "12%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (isNaN(row.transaction_type)) {
            return {
              props: {
                colSpan: 0,
              },
            }
          }
          else if (row.transaction_type === 1) {
            return <NumberFormat value={parseFloat(row.amount)} />
          }
          else {
            return "-"
          }
        }
      },
      {
        title: getLanguage({ id: "withdrawal" }),
        dataIndex: "withdrawal",
        rowKey: "withdrawal",
        width: "12%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (isNaN(row.transaction_type)) {
            return {
              props: {
                colSpan: 0,
              },
            }
          }
          else if (row.transaction_type === 2) {
            return <NumberFormat value={parseFloat(row.amount)} />
          }
          else {
            return "-"
          }
        }
      },
      {
        title: getLanguage({ id: "balance" }),
        dataIndex: "ending_balance",
        rowKey: "ending_balance",
        width: "12%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (isNaN(row.transaction_type)) {
            return (<strong>
              <NumberFormat value={parseFloat(text)} />
            </strong>)
          }
          else {
            return <NumberFormat value={parseFloat(text)} />
          }

        }

      },
    ]

    let ending_balance = null;
    let dataSource = [];

    if (this.props.pettyCashReport.opening_balance !== null) {
      ending_balance = parseFloat(this.props.pettyCashReport.opening_balance);

      dataSource.push({
        transaction_date: this.props.pettyCashReport.filter_response.start_date,
        transaction_type: getLanguage({ id: "openingBalance" }),
        petty_cash_no: getLanguage({ id: "openingBalance" }),
        ending_balance,
      })

      petty_cash_reports.map((data) => {
        if (data.transaction_type === 1) {
          ending_balance += parseFloat(data.amount)
        }
        else {
          ending_balance -= parseFloat(data.amount)
        }

        let deposit = data.transaction_type === 1 ? data.amount : null;
        let withdrawal = data.transaction_type === 2 ? data.amount : null;

        dataSource.push({
          ...data,
          deposit,
          withdrawal,
          ending_balance
        })
      })

      dataSource.push({
        transaction_date: this.props.pettyCashReport.filter_response.end_date,
        transaction_type: getLanguage({ id: "endingBalance" }),
        petty_cash_no: getLanguage({ id: "endingBalance" }),
        ending_balance,
      })

    }

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const fileName = getLanguage({ id: "pettyCashReports.pageHeader" })
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="pettyCashReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="pettyCashReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={
                () => (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                  "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin="1cm"
              scale={0.58}
              title="pettyCashReports.pageHeader"
            />

            <ExcelWrapper
              title="pettyCashReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button style={{ marginLeft: "5px" }} onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="pettyCashTypes.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_petty_cash_type"
                      )}
                      value={
                        this.state.filter.id_petty_cash_type
                          ? this.state.filter.id_petty_cash_type
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="select.pettyCashType" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loading}
                    //allowClear={true}
                    //showArrow={false}
                    >
                      {petty_cash_types.map((data, key) => (
                        <Option key={key} value={data.id_petty_cash_type}>
                          {data.petty_cash_type_code + " (" + data.description + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="purchaseInvoices.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "100%" }}
                      value={
                        this.state.filter.start_date &&
                          this.state.filter.end_date
                          ? [
                            moment(this.state.filter.start_date),
                            moment(this.state.filter.end_date)
                          ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col
                    span={12}
                    style={{ marginTop: "24px" }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.only" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter
              ? (<Empty description={<IntlMessages id="clickFilter" />} />)
              : dataSource.length === 0 &&
                !this.props.pettyCashReport.loading
                ? (
                  <Empty
                    description={
                      <IntlMessages id="pettyCashReports.table.noData" />
                    }
                  />
                )
                : (
                  <div className="isoInvoiceTable">
                    <Divider orientation="left" style={{ fontSize: 14 }}>
                      <IntlMessages id="details" />
                    </Divider>
                    <Scrollbars style={{ width: "100%" }}>
                      <TableWrapper
                        dataSource={dataSource}
                        rowKey={record => record.id_petty_cash}
                        columns={columns}
                        loading={loading}
                        className="invoiceListTable"
                        bordered={true}
                        // Set pagination, jika ada page_size mengunakan pagination API
                        // jika tidak ada page_size menggunakan pagination bawaan
                        pagination={false}
                      />
                    </Scrollbars>
                  </div>
                )
            }
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    pettyCashReport: state.pettyCashReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(PettyCashReport);
